*{
    padding: 0;
    margin: 0;
  }
    
  html{
    scroll-behavior: smooth;
  }
  
  body{
   
    font-family: 'Montserrat', sans-serif;
  
  }
  h1,h2,h3,h4,h5,h6{
    font-family: 'Roboto', sans-serif;
  }
  
  
  .navbar{
    padding: 3% 5%;
    background-color: rgba(0,0,0,0.3);
    font-size: 20px;
  
 }
  
  .nav-link{
    transition: 0.5s;
    border-radius: 5px;
    margin: 10px;
    font-style: italic;
  
  }
  .nav-link:hover{
    transform: scale(1.1);
  }
  .Header-bg{
    background-image: url('./imges/Header.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 60vh;
    background-attachment: fixed;
    width: 100%;
  }
  .product{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
    margin: 20px auto;
    border-radius: 5px;
    padding: 4% 6%;
    background-color:rgb(22, 21, 21);
    
  
  }
  .images{
    width: 100%;
    height: 150px;
    padding: 10px 10px;
  }
  
  .card{
    width: 21%;
    height:300px;
    margin-bottom: 40px;
    overflow: hidden;
    border:1px solid white;
    transition: 0.5s;
    text-transform: capitalize;
  }
  .card:hover{
    box-shadow: 0 7px 30px -10px rgba(150,170,180,0.5);
    transform: scale(1.1);
  }
  
  .Contact-bg{
      background-color: black;

  }

  .from-control{
      width: 100%;
      height: 50vh;
      padding: 7% 10%;
      
  }
  .label{
      margin-bottom: 15px;
      font-size: 20px;

  }
  .from-control input{
      padding: 10px 10px;
      width: 100%;
      border-radius: 5px;
  }
.option{
  width: 30%;
  padding: 10px 10px;
  border-radius: 5px;
}
  .Alladdress {
   
   text-align: center;

   margin-top: 40px;
  }

  
  .Details {
  color: white;
  }
  .About-bg{
    background-color: rgb(54, 52, 52);
 
  }
  .About-US{
    padding:10px 10px;
    
  }
.AboutImg{
  padding: 3% 5%;
  margin-top: 60px;
}
.colum-1{
  padding: 3% 5%;
  overflow: hidden;
}
  .colum-2{
    padding: 3% 5%;
    color: white;
    
  }
  .Footer-bg{
    background-color: rgb(8, 8, 7);
    margin-top: 0;
  }
 .Footer{
   color: white;
   height: 200px;
   padding: 3% 5%;
 }

 @media screen and (max-width:600px) {
   .card{
     width: 100%;
   }
   .Details{
     margin-top: 100px;
     
   }
  
     
 }
@media screen and (max-width:800px) {
  .card{
    width: 50%;
  }
  .Details{
    margin-top: 150px;
  }
 
}
@media screen and (max-width:1000px) {
  .Details{
margin-top: 100px;
  }
}